body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form__label {
  display: block;
  margin: .5rem 0;
  font-weight: bold;
  line-height: 1.3; }
  .form__label input,
  .form__label textarea {
    display: block;
    border: 1px solid #353535;
    padding: .5rem .3rem;
    border-radius: .2rem;
    resize: none;
    width: 90%; }

.form {
  background-color: #F1FFFF;
  padding: .5rem;
  border-radius: 5px;
  box-shadow: -1px 2px 5px #353535;
  max-width: 200px;
  margin-top: 0; }
  .form input, .form textarea {
    background-color: #FFFF; }
  .form .btn-cadastro {
    margin: 0 .6rem; }

table {
  background-color: #F1FFFF;
  max-width: 900px;
  max-height: 65vh;
  display: inline-block;
  box-shadow: -1px 2px 5px #353535;
  padding: 1rem;
  line-height: 2;
  overflow: auto;
  border-spacing: 10rem;
  border-collapse: collapse; }
  table #marca {
    width: 10%; }
  table #ano {
    width: 10%; }
  table #placa {
    width: 10%; }
  table #obs {
    width: 20%; }
  table th, table td {
    max-width: 200px;
    word-wrap: break-word;
    padding: 3px;
    border-width: 1px;
    border-style: solid;
    border-color: #448B8C; }
  table caption {
    font-weight: bolder;
    font-size: 1.5rem;
    padding: .5rem 0 1rem; }

.table {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end; }
  .table .btn-primary {
    margin-top: 1rem; }

.container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  justify-items: center; }

html,body{
    box-sizing: border-box;
    padding: .5rem;
    color: #035354;
    background-color: #CFF5EE;
}

h1{
    margin: 1rem;
}

.btn{
    padding: .5rem .8rem;
    display: inline-block;
    border-radius: 5px;
    box-shadow: -1px 1px 2px #353535;
    text-decoration: none;
    color: #F1FFFF;
    cursor: pointer;
    background-color: #50A5A6;
    transition: .5s;
}

.btn:hover{
    background-color: #448B8C;
}
